import { Box, ImageList, ImageListItem, Container, Grid } from "@mui/material";
import PermMediaIcon from "@mui/icons-material/PermMedia";
import Title from "./Title";

export default function Portfolio() {
  return (
    <Container
      id="portfolio"
      sx={{
        margin: "0 auto",
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: "2rem",
      }}
    >
      <Box>
        <Title text="GALLERY" icon={<PermMediaIcon fontSize="large" />} />
        <Grid container spacing={1}>
          <Grid item xs={6} md={3}>
            <img
              src="https://images.unsplash.com/photo-1549388604-817d15aa0110"
              alt=""
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item xs={6} md={3}>
            <img
              src="https://images.unsplash.com/photo-1549388604-817d15aa0110"
              alt=""
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item xs={6} md={3}>
            <img
              src="https://images.unsplash.com/photo-1574180045827-681f8a1a9622"
              alt=""
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item xs={6} md={3}>
            <img
              src="https://images.unsplash.com/photo-1525097487452-6278ff080c31"
              alt=""
              style={{ width: "100%" }}
            />
          </Grid>
        </Grid>
      </Box>
      {/* <Box sx={{ width: 500, height: "80vh", overflowY: 'scroll' }}>
        <ImageList variant="masonry" cols={3} gap={10}>
          {itemData.map((item) => (
            <ImageListItem key={item.img} elevation={10}>
              <img
                srcSet={`${item.img}?w=161&fit=crop&auto=format 1x,
                ${item.img}?w=161&fit=crop&auto=format&dpr=2 2x`}
                alt={item.title}
                loading="lazy"
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Box> */}
    </Container>
  );
}

